.reacts {
  width: 740px;
  height: 564px;
  inset: 50% auto auto 50%;
  margin-right: -50%;
  -webkit-transform: translate(45%, 3%);
          transform: translate(45%, 3%);
  z-index: 1;
  padding: 0px;
  transition: all 0.5s ease-out 0s;
  overflow-y: auto;
  outline: none;
  background-color: white;
}

@media (max-width: 1024px) {
  .reacts {
    width: 740px;
    height: 506px;
    inset: 50% auto auto 50%;
    margin-right: -50%;
    -webkit-transform: translate(20%, 3%);
            transform: translate(20%, 3%);
    z-index: 1;
    padding: 0px;
