.card-info {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-column-gap: 2rem;
  -webkit-column-gap: 2rem;
          column-gap: 2rem;
  grid-row-gap: 2rem;
  row-gap: 2rem;
}

.card-contenedor {
  width: 326px;
  display: grid;
  height: 140px;
  grid-template-columns: repeat(2, 0.3fr);
  border: 1px solid #e0e0e0;

  border-radius: 10px;
}

.card-img img {
  width: 140px;
  height: 140px;
  border-radius: 10px 0px 0px 10px;
}

.card-contenido {
  width: 11.5rem;
  padding: 0.5rem 1rem;
}

